@import url(https://fonts.googleapis.com/css?family=Raleway:400,500,700);
@import url(https://code.ionicframework.com/ionicons/2.0.1/css/ionicons.min.css);
@import url('https://fonts.googleapis.com/css?family=DM+Sans:400,500,700&display=swap');


.background-img {

  background: url('../Assets/img.jpg');
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: 50% 28%;
  height: 60vh;

}

@media screen and (max-width: 786px) {
  .background-img {
    margin: auto;
  }
}

.button {
  box-shadow: inset 0 0 0 0 #FDBE33;
  transition: 0.4s ease-in-out;
}

.button:hover {
  color: #20212B;
  box-shadow: inset 10px -20px 10px 0px #FDBE33;
}

figure.snip1477 {
  font-family: 'Raleway', Arial, sans-serif;
  position: relative;
  overflow: hidden;
  margin: 10px;
  min-width: 230px;
  max-width: 315px;
  width: 100%;
  color: #ffffff;
  text-align: center;
  font-size: 16px;
  background-color: #000000;
}

figure.snip1477 *,
figure.snip1477 *:before,
figure.snip1477 *:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-transition: all 0.55s ease;
  transition: all 0.55s ease;
}

figure.snip1477 img {
  max-width: 100%;
  backface-visibility: hidden;
  vertical-align: top;
  opacity: 0.9;
}

figure.snip1477 .title {
  position: absolute;
  top: 58%;
  left: 25px;
  padding: 5px 10px 10px;
}

figure.snip1477 .title:before,
figure.snip1477 .title:after {
  height: 2px;
  width: 400px;
  position: absolute;
  content: '';
  background-color: #ffffff;
}

figure.snip1477 .title:before {
  top: 0;
  left: 10px;
  -webkit-transform: translateX(100%);
  transform: translateX(100%);
}

figure.snip1477 .title:after {
  bottom: 0;
  right: 10px;
  -webkit-transform: translateX(-100%);
  transform: translateX(-100%);
}

figure.snip1477 .title div:before,
figure.snip1477 .title div:after {
  width: 2px;
  height: 300px;
  position: absolute;
  content: '';
  background-color: #ffffff;
}

figure.snip1477 .title div:before {
  top: 10px;
  right: 0;
  -webkit-transform: translateY(100%);
  transform: translateY(100%);
}

figure.snip1477 .title div:after {
  bottom: 10px;
  left: 0;
  -webkit-transform: translateY(-100%);
  transform: translateY(-100%);
}

figure.snip1477 h6,
figure.snip1477 h5 {
  margin: 0;
  text-transform: uppercase;
}

figure.snip1477 h6 {
  font-weight: 400;
}

figure.snip1477 h5 {
  display: block;
  font-weight: 700;
  background-color: #ffffff;
  padding: 5px 10px;
  color: #000000;
}

figure.snip1477 figcaption {
  position: absolute;
  bottom: 42%;
  left: 25px;
  text-align: left;
  opacity: 0;
  padding: 5px 60px 5px 10px;
  font-size: 0.8em;
  font-weight: 500;
  letter-spacing: 1.5px;
}

figure.snip1477 figcaption p {
  margin: 0;
}

figure.snip1477 a {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

figure.snip1477:hover img,
figure.snip1477.hover img {
  zoom: 1;
  filter: alpha(opacity=35);
  -webkit-opacity: 0.35;
  opacity: 0.35;
}

figure.snip1477:hover .title:before,
figure.snip1477.hover .title:before,
figure.snip1477:hover .title:after,
figure.snip1477.hover .title:after,
figure.snip1477:hover .title div:before,
figure.snip1477.hover .title div:before,
figure.snip1477:hover .title div:after,
figure.snip1477.hover .title div:after {
  -webkit-transform: translate(0, 0);
  transform: translate(0, 0);
}

figure.snip1477:hover .title:before,
figure.snip1477.hover .title:before,
figure.snip1477:hover .title:after,
figure.snip1477.hover .title:after {
  -webkit-transition-delay: 0.15s;
  transition-delay: 0.15s;
}

figure.snip1477:hover figcaption,
figure.snip1477.hover figcaption {
  opacity: 1;
  -webkit-transition-delay: 0.2s;
  transition-delay: 0.2s;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  width: 100%;
  height: 100vh;
  font-family: 'DM-sans', sans-serif;
}

.header {
  font-size: 24px;
  text-align: center;
}

.content {
  display: flex;
  flex-wrap: wrap;
  margin-top: 60px;
  padding: 0 30px;
}

.wrapper {
  width: 33.3%;
  height: 100%;
  padding: 10px;
}

.name {
  position: relative;
  font-size: 16px;
  display: inline-block;

  &:after {
    content: '';
    position: absolute;
    width: calc(100% + 10px);
    height: 1px;
    background-color: #000;
    bottom: -4px;
    left: 0;
  }
}

.box {
  position: relative;
  max-height: 300px;
  border-radius: 4px;
  overflow: hidden;
  box-shadow:
    0 1.4px 1.7px rgba(0, 0, 0, 0.017),
    0 3.3px 4px rgba(0, 0, 0, 0.024),
    0 6.3px 7.5px rgba(0, 0, 0, 0.03),
    0 11.2px 13.4px rgba(0, 0, 0, 0.036),
    0 20.9px 25.1px rgba(0, 0, 0, 0.043),
    0 50px 60px rgba(0, 0, 0, 0.06);

  .hide {
    opacity: 0;
  }

  .frame {
    position: absolute;
    border: 1px solid #fff;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  h2,
  p {
    position: absolute;
    color: #fff;
    z-index: 2;
    width: 100%;
    transition: opacity 0.2s, transform 0.3s;
  }

  h2 {
    font-weight: 500;
    font-size: 22px;
    margin-bottom: 0;
    letter-spacing: 1px;
  }

  p {
    bottom: 0;
    font-size: 14px;
    letter-spacing: 1px;
  }

  &:hover {
    transition: all .3s ease-in-out;
  }

  &:hover:before {
    transition: all .3s ease-in-out;
  }

  img {
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 1;
    transition: all .3s ease-in-out;

    &:hover {
      transition: all .3s ease-in-out;
    }

    &:after {
      content: '';
      position: absolute;
      background-color: rgba(0, 0, 0, .6);
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      opacity: 0;
    }

    &:hover {
      transition: all .3s ease-in-out;
    }
  }
}

.zoom-in {
  h2 {
    top: 50%;
    transform: translatey(-50%);
    text-align: center;
    margin: 0;
  }

  p {
    text-align: center;
    top: calc(50% + 40px);
    transition: all .3s ease;
    transform: scale(1.8);
    opacity: 0;
  }

  &:hover {
    img {
      transform: scale(1.1);
      filter: grayscale(100%);
    }

    p {
      transform: scale(1);
      transition: all .3s ease;
      opacity: 1;
    }
  }
}

.w-content {
  .frame {
    width: calc(100% - 100px);
    height: calc(100% - 100px);
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-color: transparent;
  }

  h2 {
    margin-top: 0;
    top: 50%;
    transform: translatey(-50%);
    text-align: center;
    letter-spacing: 1px;
  }

  p {
    transform: translate3d(0, -10px, 0);
  }

  &:hover {
    .frame {
      border-color: #fff;
      transition: color .3s ease-in-out, all .3s ease-in-out;
      width: calc(100% - 20px);
      height: calc(100% - 20px);
    }

    opacity: .8;
  }
}

.postcard {
  h2 {
    top: 50%;
    text-align: left;
    transform: translate3d(50px, 20px, 0);
  }

  p {
    transform: translate3d(40px, 60px, 0);
    font-size: 24px;
    letter-spacing: 1px;
  }

  &:hover {
    h2 {
      transform: translate3d(20px, 20px, 0);
    }

    p {
      opacity: 1;
    }

    img {
      filter: opacity(0.7);
      transform: translate3d(-30px, 0, 0);
    }
  }
}

.blury-card {
  .frame {
    width: calc(100% - 40px);
    height: calc(100% - 40px);
    opacity: 0;
    transition: all .3s ease-in-out
  }

  h2,
  p {
    text-align: center;
    top: 50%;
    transform: translatey(-50%);
    margin: 0;
    z-index: 3;
  }

  p {
    transform: translatey(30px);
    letter-spacing: 3px;
  }

  span {
    font-size: 24px;
  }

  &:before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: #008E6B;
    z-index: 1;
    left: 0;
    opacity: 0;
    transition: all .3s ease-in-out;
  }

  img {
    z-index: 2;
  }

  &:hover {
    .frame {
      width: calc(100% - 120px);
      height: calc(100% - 120px);
      opacity: 1;
    }

    img {
      opacity: .7;
    }

    &:before {
      opacity: 1;
      transition: all .3s ease-in-out;
    }
  }
}

.vintage {
  h2 {
    top: 50%;
    transform: translate3d(0, 60px, 0);
    text-align: center;
  }

  p {
    opacity: 0;
    bottom: 0;
    transform: translate3d(0, -10px, 0);
    font-size: 14px;
    letter-spacing: 1px;
    text-align: center;
  }

  &:before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: linear-gradient(to bottom, rgba(72, 76, 97, 0) 0%, rgba(72, 76, 97, 0.8) 75%);
    z-index: 2;
    bottom: -100%;
    left: 0;
  }

  &:hover:before {
    bottom: 0;
  }

  &:hover {
    h2 {
      bottom: 40px;
      transform: translate3d(0, 20px, 0);
    }

    p {
      opacity: 1;
      transform: translate3d(0, -30px, 0);
    }
  }
}

.zoom-out {
  .frame {
    width: calc(100% - 100px);
    height: calc(100% - 100px);
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: calc(100% - 40px);
    height: calc(100% - 40px);
  }

  h2 {
    font-size: 20px;
    font-weight: 300;
    margin-left: 10px;
    letter-spacing: 1px;
  }

  span {
    font-size: 24px;
    font-weight: 500;
  }

  p {
    max-width: 120px;
    text-align: right;
    margin: 10px;
    right: 0;
  }

  img {
    transform: scale(1.1);
  }

  &:hover img {
    transform: scale(1);
    filter: contrast(70%);
  }
}

@media screen and (max-width: 880px) {
  .wrapper {
    width: 50%;
  }
}

@media screen and (max-width: 520px) {
  .wrapper {
    width: 100%;
  }
}


.carousel-inner img {
  width: 100%;
  height: auto; /* Maintain aspect ratio */
}

/* Ensure the carousel content is centered */
.carousel-item .position-absolute {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .carousel-item h3 {
    font-size: 1.5rem;
  }

  .carousel-item p {
    font-size: 1rem;
  }

  .carousel-item .btn {
    font-size: 0.875rem;
    padding: 0.5rem 1rem;
  }

  .carousel-item {
    min-height: 300px;
  }
}

@media (max-width: 576px) {
  .carousel-item h3 {
    font-size: 1.25rem;
  }

  .carousel-item p {
    font-size: 0.875rem;
  }

  .carousel-item .btn {
    font-size: 0.75rem;
    padding: 0.375rem 0.75rem;
  }

  .carousel-item {
    min-height: 250px;
  }
}